<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>商品添加</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="parames" label-width="80px">
                    <el-form-item label="商品标题">
                        <el-input placeholder="请输入商品标题" v-model="parames.title" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="作者">
                        <el-input placeholder="请输入作者名" v-model="parames.author" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="出版社">
                        <el-input placeholder="请输入出版社" v-model="parames.publisher" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="商品分类">
                        <el-select
                            v-model="parames.categoryIds"
                            multiple
                            placeholder="请选择"
                            style="width:220px"
                        >
                            <el-option
                                v-for="item in categoryOptions"
                                :key="item.categoryId"
                                :label="item.categoryName"
                                :value="item.categoryId"
                            ></el-option>
                        </el-select>
                        <el-button
                            class="button-new-tag"
                            size="small"
                            @click="showCategoryBox"
                        >+ 添加分类</el-button>
                    </el-form-item>
                    <el-form-item label="商品标签">
                        <el-tag
                            :key="tag"
                            v-for="tag in parames.tags"
                            closable
                            :disable-transitions="false"
                            @close="handleClose(tag)"
                        >{{tag}}</el-tag>
                        <el-input
                            class="input-new-tag"
                            v-if="inputVisible"
                            v-model="inputValue"
                            ref="saveTagInput"
                            @keyup.enter.native="handleInputConfirm"
                            @blur="handleInputConfirm"
                        ></el-input>
                        <el-button v-else class="button-new-tag" @click="showInput">+ 增加</el-button>
                    </el-form-item>
                    <el-form-item label="主图">
                        <images-upload :moreUpload="false" :index="1" :onSuccess="uploadSuccess"></images-upload>
                    </el-form-item>
                    <el-form-item label="轮播图">
                        <images-upload
                            :onSuccess="uploadSuccess"
                            :index="2"
                            :onRemove="removeRemove"
                            :memory="1"
                        ></images-upload>
                    </el-form-item>
                    <el-form-item label="图文详情">
                        <images-upload
                            :onSuccess="uploadSuccess"
                            :index="3"
                            :onRemove="removeRemove"
                            :memory="2"
                        ></images-upload>
                    </el-form-item>
                    <el-form-item label="币种">
                        <el-select
                            v-model="parames.currencyType"
                            placeholder="请选择"
                            style="width:220px"
                        >
                            <el-option
                                v-for="item in moneyOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="原价">
                        <el-input placeholder="请输入原价（单位分）" v-model="parames.reservePrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="优惠价">
                        <el-input placeholder="请指定一个优惠价（单位分）" v-model="parames.finalPrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="积分">
                        <el-input placeholder="默认按系统分配" v-model="parames.balance" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="库存">
                        <el-input placeholder="请输入库存量" v-model="parames.quantity" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="预约时间">
                        <el-date-picker
                            value-format="timestamp"
                            v-model="parames.onSaleTime"
                            type="datetime"
                            placeholder="选填"
                            align="right"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="包邮">
                        <el-switch v-model="noPostage"></el-switch>
                    </el-form-item>
                    <el-form-item label="邮费" v-if="!noPostage">
                        <el-input placeholder="请输入邮费（单位分）" v-model="parames.freightPrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="满包邮" v-if="!noPostage">
                        <el-input placeholder="满多少金额免邮费（单位分）" v-model="parames.freePrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="商品sku">
                        <div>
                            <div class="stand">
                                <ul>
                                    <li class="attr" v-for="(item,index) in standData" :key="index">
                                        <el-button
                                            type="danger"
                                            icon="el-icon-delete"
                                            @click="remove(index)"
                                            style="margin-right:10px"
                                        >删除</el-button>
                                        <el-input
                                            class="title"
                                            v-model="item.title"
                                            placeholder="请输入属性"
                                            style="width:100px"
                                            clearable
                                        ></el-input>
                                        <div v-if="item.attrs.length">
                                            <div
                                                class="putt"
                                                v-for="(subitem,i) in item.attrs"
                                                :key="i"
                                            >
                                                <el-input
                                                    v-model="subitem.attr"
                                                    style="width:100px"
                                                    placeholder="请输入值"
                                                    clearable
                                                ></el-input>
                                                <div class="close" @click="closeAtrr(index,i)">×</div>
                                            </div>
                                        </div>
                                        <el-button
                                            type="primary"
                                            icon="el-icon-circle-plus-outline"
                                            @click="addAtrr(index)"
                                        >添加属性值</el-button>
                                    </li>
                                </ul>
                                <div class="add">
                                    <el-button type="primary" @click="addStand">添加属性</el-button>
                                    <el-button type="primary" @click="getTable">生成sku列表</el-button>
                                    <el-button type="primary" @click="read">使用默认sku属性</el-button>
                                </div>
                            </div>
                            <div class="table">
                                <el-table
                                    v-if="isTable"
                                    :data="tableData"
                                    border
                                    style="width: 100%"
                                >
                                    <el-table-column prop="skuValue" label="sku信息" min-width="100"></el-table-column>
                                    <el-table-column label="原价（单位分）" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.price"
                                                placeholder="请输入该sku原价"
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="优惠价（单位分）" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.promotionPrice"
                                                placeholder="请输入该sku优惠价"
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="积分" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.balance"
                                                placeholder="为空按系统分配"
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="库存" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.quantity"
                                                placeholder="请输入该sku库存量"
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </div>
                    </el-form-item>
                    
                    <el-form-item>
                        <el-button @click="onSubmit(false)">返回</el-button>
                        <el-button type="primary" @click="onSubmit(true,1)">保存并上架</el-button>
                        <el-button type="primary" @click="onSubmit(true,0)">保存为预约</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "goodsAdd",
    data() {
        return {
            inputVisible: false, //添加标签显示input
            inputValue: "", //输入标签input值

            parames: {
                title: "",
                pictUrl: "", //主图
                picCharts: [], //轮播图
                picDetails: [], //图文
                reservePrice: "", //原价
                finalPrice: "", //优惠价
                tags: ["热门推荐", "特价专区"], //标签
                categoryIds: [1], //分类
                currencyType: 0, //币种
                status: 1, //0：待售 1：上架  2：下架 3：删除
                onSaleTime: "", //预约时间
                quantity: "", //默认库存
                goodsPropertyRefList: [],
                goodsSkuList: [],
                publisher:"",
                author:"",
                freightPrice:"",//统一邮费
                freePrice:"",//满多少包邮
                balance:"",//积分
            },
            categoryOptions: [],
            //币种
            moneyOptions: [
                {
                    value: 0,
                    label: "人民币"
                },
                {
                    value: 1,
                    label: "港币"
                }
            ],
            noPostage:true,//包邮
            //sku信息
            tableData: [],
            input: "",
            isTable: false,
            standData: [],
            list: [],
            group: []
        };
    },
    created() {
        this.getCategoryList();
    },
    methods: {
        //分类列表
        async getCategoryList() {
            const data = await this.$api.CategoryList({
                pageNo: 1,
                pageSize: 100
            });
            this.categoryOptions = data;
        },
        //添加分类
        async addCategory(categoryName) {
            const data = await this.$api.CategoryAdd({
                categoryName,
                sortOrder: 1
            });
            this.getCategoryList();
        },
        //添加商品
        async addGoods(categoryName) {
            const data = await this.$api.GoodsAdd(this.parames);
            //返回列表刷新页面
            this.$router.push({
                name: "goodsList",
                params: {
                    onload: true,
                    onloadDeep: true
                }
            });
        },
        //添加分类弹框
        showCategoryBox() {
            this.$prompt("请输入分类名", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消"
            })
                .then(({ value }) => {
                    if (value === "") {
                        this.$message({
                            message: "分类名不能为空",
                            type: "warning"
                        });
                    } else {
                        this.addCategory(value);
                    }
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消输入"
                    });
                });
        },
        //删除标签
        handleClose(tag) {
            this.parames.tags.splice(this.parames.tags.indexOf(tag), 1);
        },
        //标签输入
        showInput() {
            this.inputVisible = true;
            this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        //添加标签
        handleInputConfirm() {
            let inputValue = this.inputValue;
            if (inputValue) {
                this.parames.tags.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = "";
        },
        //图片添加成功
        uploadSuccess(imgUrl, index) {
            if (index == 1) {
                this.parames.pictUrl = imgUrl;
            } else if (index == 2) {
                this.parames.picCharts.push(imgUrl);
            } else if (index == 3) {
                this.parames.picDetails.push(imgUrl);
            }
        },
        //图片删除
        removeRemove(imgUrl, index) {
            if (index == 2) {
                this.parames.picCharts.splice(
                    this.parames.picCharts.findIndex(item => item === imgUrl),
                    1
                );
            } else if (index == 3) {
                this.parames.picDetails.splice(
                    this.parames.picDetails.findIndex(item => item === imgUrl),
                    1
                );
            }
        },

        // 添加规格行
        addStand(i) {
            //  限制规格种类不超过4种
            if (this.standData.length > 3) {
                this.$message("不能超过四行");
            } else {
                this.standData.push({ title: "", attrs: [] });
            }
        },
        // 添加sku表格
        getTable() {
            this.isTable = true;
            this.tableData = [];
            this.group = [];
            this.list = [];

            const num = this.standData.length;
            this.standData.forEach(item => {
                this.list.push(item.attrs);
            });

            var arr = [];
            var that = this;
            function func(skuarr = [], i) {
                for (let j = 0; j < that.list[i].length; j++) {
                    if (i < that.list.length - 1) {
                        skuarr[i] = that.list[i][j].attr;
                        func(skuarr, i + 1);
                    } else {
                        arr.push([...skuarr, that.list[i][j].attr]);
                    }
                }
                return arr;
            }
            let newList = func([], 0);
            let b;
            newList.forEach(item => {
                b = "";
                for (let i = 0; i < num; i++) {
                    let a = this.standData[i].title;
                    a = a + ":" + item[i];
                    b = b + a + ";";
                }
                b = b.substring(0, b.length - 1);
                this.group.push(b);
            });
            let table = [];
            for (let j = 0; j < this.group.length; j++) {
                table.push({
                    skuValue: this.group[j],
                    price: "",
                    quantity: "",
                    promotionPrice: "",
                    balance:""
                });
            }
            this.tableData = table;
        },

        // 删除规格行
        remove(i) {
            this.standData.splice(i, 1);
        },
        // 添加属性值
        addAtrr(i) {
            //  限制属性值不超过5个
            if (this.standData[i].attrs.length > 4) {
                this.$message("不能超过5个");
            } else {
                // 存的时候是存的对象
                this.standData[i].attrs.push({ attr: "" });
            }
        },
        // 删除属性值
        closeAtrr(a, b) {
            this.standData[a].attrs.splice(b, 1);
        },

        // 默认属性
        read() {
            // 默认数据
            const arr = ["单位:本;", "单位:套;"];
            const a = arr[0].split(";");
            const num = a.length - 1;
            let ss = [];
            for (let tt = 0; tt < num; tt++) {
                ss.push([]);
            }

            arr.forEach(item => {
                for (let tt = 0; tt < num; tt++) {
                    ss[tt].push(item.split(";")[tt].split(":")[1]);
                }
            });
            ss = ss.map(item => {
                return Array.from(new Set(item));
            });
            for (let s = 0; s < ss.length; s++) {
                for (let t = 0; t < ss[s].length; t++) {
                    ss[s][t] = { attr: ss[s][t] };
                }
            }
            for (let i = 0; i < num; i++) {
                this.standData.push({
                    title: a[i].split(":")[0],
                    attrs: ss[i]
                });
            }
        },

        onSubmit(type, status) {
            if (!type) {
                this.$router.go(-1);
                return;
            }
            this.parames.status = status;
            if (status == 0 && this.parames.onSaleTime == "") {
                this.$message.error("请添加预售时间");
                return;
            }
            if (this.tableData.length > 0) {
                this.parames.goodsSkuList = this.tableData;
                for (let key in this.standData) {
                    let obj = {
                        propertyName: this.standData[key].title,
                        valueAliasList: []
                    };
                    for (let i in this.standData[key].attrs) {
                        obj.valueAliasList.push(
                            this.standData[key].attrs[i].attr
                        );
                    }
                    this.parames.goodsPropertyRefList.push(obj);
                }
            }
            this.addGoods();
        }
    },
    watch:{
        noPostage(n){
            if(n){
                this.parames.freightPrice=""
                this.parames.freePrice=""
            }
        }
    }
};
</script>

<style scoped >
ul {
    list-style: none;
    padding: 0;
}
ul li {
    display: flex;
    flex-wrap: wrap;
}
.attr {
    margin-bottom: 10px;
}
.el-input {
    width: auto;
}
.putt {
    display: inline-block;
    position: relative;
    margin-right: 10px;
}

.title {
    background-color: bisque;
    margin-right: 10px;
}
.close {
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: red;
    color: #fff;
    border-radius: 50%;
    line-height: 15px;
    text-align: center;
    right: -5px;
    top: -5px;
}
</style>